<template>
    <div>
        <a-form :layout="formLayout"
                @submit="handleSearch(false)"
                :autoFormCreate="(form)=>{this.form = form}">
            <template v-for="row in fields">
                <a-form-item
                        v-if="formIf(row,['user_id'])"
                        :colon="false"
                        :label='row.label'
                        :labelCol="formItemLayout.labelCol"
                        :wrapperCol="formItemLayout.wrapperCol"
                        :fieldDecoratorId="row.field"
                >
                    <SelectUser/>
                </a-form-item>
                <a-form-item
                        v-if="formIf(row,['category_id'])"
                        :colon="false"
                        :label='row.label'
                        :labelCol="formItemLayout.labelCol"
                        :wrapperCol="formItemLayout.wrapperCol"
                        :fieldDecoratorId="row.field"
                >
                    <SelectCategory/>
                </a-form-item>
                <a-form-item
                        v-else-if="formIf(row,['department_id'])"
                        :colon="false"
                        :label='row.label'
                        :labelCol="formItemLayout.labelCol"
                        :wrapperCol="formItemLayout.wrapperCol"
                        :fieldDecoratorId="row.field"
                >
                    <SelectDepartment/>
                </a-form-item>
                <a-form-item
                        v-else-if="formIf(row,['start_timestamp','end_timestamp'])"
                        :colon="false"
                        :label='row.label'
                        :labelCol="formItemLayout.labelCol"
                        :wrapperCol="formItemLayout.wrapperCol"
                        :fieldDecoratorId="row.field"
                >
                    <DateTime/>
                </a-form-item>
                <a-form-item
                        v-else-if="formIf(row,['keyword'])"
                        :colon="false"
                        :label='row.label'
                        :labelCol="formItemLayout.labelCol"
                        :wrapperCol="formItemLayout.wrapperCol"
                        :fieldDecoratorId="row.field"
                >
                    <a-input :placeholder="'请输入'+row.label"/>
                </a-form-item>
            </template>
            <a-form-item
                    v-if="fields.length"
                    :wrapperCol="buttonItemLayout.wrapperCol"
            >
                <a-button-group>
                    <a-button type='primary' @click="handleSearch(false)">搜索</a-button>
                    <a-button @click="handleSearch(true)">清空</a-button>
                </a-button-group>
            </a-form-item>
            <a-form-item
                    :wrapperCol="buttonItemLayout.wrapperCol"
            >
                <a-button type='primary' @click="handleExport()">导出</a-button>
            </a-form-item>
        </a-form>
    </div>
</template>

<script>
    import SelectCategory from '@/components/select/SelectCategory'
    import SelectUser from '@/components/select/SelectUser'
    import SelectDepartment from '@/components/select/SelectDepartment'
    import DateTime from '@/components/select/DateTime'

    export default {
        components: {SelectCategory,SelectUser, SelectDepartment, DateTime},
        props: {
            fields: {
                required: true,
                type: Array,
            }
        },
        data() {
            return {
                formLayout: 'inline',
                form: {},
            }
        },
        methods: {
            formIf(row, fieldRuleArray) {
                for (let i = 0; i < fieldRuleArray.length; i++) {
                    if (fieldRuleArray[i] === row.field) {
                        return true;
                    }
                }
                return false;
            },
            handleFormLayoutChange(e) {
                this.formLayout = e.target.value
            },
            handleSearch(isClear = false) {
                console.log(isClear);
                let data = {};
                if (isClear) {
                    for (let i = 0; i < this.fields.length; i++) {
                        let map = {};
                        map[this.fields[i].field] = null;
                        console.log(map);
                        this.form.setFieldsValue(map)
                    }
                } else {
                    data = this.form.getFieldsValue();

                }
                this.$emit('handleSearch', data);
            },
            handleExport(){
                this.$message.success('功能开发中');
            }
        },
        computed: {
            formItemLayout() {
                const {formLayout} = this
                return formLayout === 'horizontal' ? {
                    labelCol: {span: 4},
                    wrapperCol: {span: 14},
                } : {}
            },
            buttonItemLayout() {
                const {formLayout} = this
                return formLayout === 'horizontal' ? {
                    wrapperCol: {span: 14, offset: 4},
                } : {}
            },
        },
    }
</script>
