<template>
    <div>
        <div v-for="(question,index) in questions">
            <p>
                {{index+1}}.{{question.title}}
                <template v-if="!!question.user_answers_format">

                    <template v-if="question.hasOwnProperty('is_correct')">
                        <template v-if="!!question.is_correct">
                            回答正确！正确答案：{{getAnswer(question)}}
                        </template>
                        <template v-else>
                            回答错误。正确答案：{{getAnswer(question)}}
                        </template>
                    </template>

                    <template v-else>
                        ( <b style="color: #DA3639;">{{question.user_answers_format}}</b> )
                    </template>

                </template>
            </p>
            <a-radio-group
                    v-if="question.answers.length<=1"
                    :value="getValue('radio',question)"
                    @change="val=>{setAnswers(val,index,'radio')}">
                <a-radio v-for="(option,key) in question.options"
                         :key="key"
                         :value="key">
                    {{key+". "+option}}
                </a-radio>
            </a-radio-group>

            <a-checkbox-group
                    v-else
                    :value="getValue('checkbox',question)"
                    @change="val=>{setAnswers(val,index,'checkbox')}">
                <a-checkbox v-for="(option,key) in question.options"
                            :key="key"
                            :value="key"
                            >
                    {{key+". "+option}}
                </a-checkbox>
            </a-checkbox-group>
            <div class="line_xx"></div>
        </div>
    </div>
</template>

<script>
    export default {
        props: {
            questions: {type: Array},
        },
        model: {
            prop: 'questions',
            event: 'change'
        },
        computed: {},
        methods: {
            setAnswers(obj, index, type) {
                let copy = JSON.parse(JSON.stringify(this.questions));

                switch (type) {
                    case 'radio':
                        this.$set(copy[index], 'user_answers', [obj.target.value])
                        this.$set(copy[index], 'user_answers_format', obj.target.value)
                        break;
                    case 'checkbox':
                        let arr = obj;
                        arr.sort();
                        this.$set(copy[index], 'user_answers', arr)
                        this.$set(copy[index], 'user_answers_format', arr.join(','))
                        break;
                }

                this.$emit('change', copy);
            },
            getAnswer(question) {
                return question.answers.toString();
            },
            getValue(type, question) {
                let user_answers = question.user_answers;
                let result = null;
                switch (type) {
                    case 'radio':
                        if (user_answers && user_answers.length) {
                            result = user_answers[0];
                        }
                        break;
                    case 'checkbox':
                        result = user_answers;
                        break;
                }
                console.log(result);
                return result;
            }
        },
    }
</script>
