<template>
    <a-tree-select
            :style="styleValue"
            :treeData="treeData"
            :value="getValue(value)"
            @change="onChange"
            searchPlaceholder='请点击选择'
            treeNodeFilterProp='label'
            :disabled="disabled"
    />
</template>

<script>
    import {TreeSelect} from 'ant-design-vue'
    import {getDepartmentUsersSelectTreeList} from '@/api/department'

    export default {
        props: {
            value: {
                required: false,
                default() {
                    return ''
                }
            },
            styleValue: {
                required: false,
                default: () => {
                    return {'min-width': '200px'}
                }
            },
            disabled: {
                required: false,
                type: Boolean,
                default() {
                    return false;
                }
            },
        },
        model: {
            prop: 'value',
            event: 'change'
        },
        data() {
            return {
                treeData: [],
            }
        },
        methods: {
            getValue(value) {
                return [value]
            },
            init() {
                getDepartmentUsersSelectTreeList()
                    .then(res => {
                        this.treeData = res;
                    })
            },
            onChange(value) {
                if (value === undefined) {
                    this.$message.warning('选择错误，请重新选择');
                    return false;
                }

                this.$emit('change', value ? parseInt(value) : 0);
            },
        },
        watch: {},
        created() {
            this.init();
        }
    }
</script>
