<template>
    <div>
        <TopButtons/>
        <div style="height: 10px"></div>
        <filter-component :fields="filterFields" @handleSearch="handleSearch"/>
        <div style="height: 10px"></div>

        <div id="split-pane-container" style="height: 400px">
            <split-pane @resize="resize" :min-percent='20' :default-percent='70' split="vertical">
                <template slot="paneL">
                    <table-component
                            ref="table"
                            @handleOperate="handleOperate"
                            :columns="columns"
                            :rowSelection="rowSelection"
                            :request="tableRequest">
                    </table-component>
                </template>
                <template slot="paneR">
                    <a-card>
                        <Questions v-if="questions.length" :questions="questions"></Questions>
                        <div v-else>
                            <h1>请选择题目</h1>
                        </div>
                    </a-card>
                </template>
            </split-pane>
        </div>
    </div>
</template>

<script>
    import TopButtons from '../在线考试列表/components/TopButtons'
    import {getCollectQuestionList, deleteCollectQuestion} from '@/api/examCollectQuestion'
    import tableComponent from '@/components/table'
    import filterComponent from '@/components/filter/Index'
    import Questions from '@/components/exam/questions'

    export default {
        components: {
            TopButtons, tableComponent, filterComponent, Questions
        },
        data() {
            return {
                modalTitle: null,
                modalVisible: false,
                formId: 0,
                tableRequest: getCollectQuestionList,
                filterFields: [
                    {label: '关键字', field: 'keyword'},
                    {label: '日期', field: 'start_timestamp'},
                    {label: '-', field: 'end_timestamp'},
                ],
                columns: [
                    {
                        title: '#',
                        dataIndex: 'index',
                        scopedSlots: {customRender: 'index'},
                    },
                    {
                        title: '题目',
                        dataIndex: 'title',
                        key: 'title',
                    },
                    {
                        title: '操作',
                        dataIndex: 'operation',
                        scopedSlots: {customRender: 'operation'},
                        buttons: {
                            delete: {visible: true, disable: false,},
                        }
                    }
                ],
                tableHeight: '0px',
                questions: [],
                rowSelection: {
                    onChange: (selectedRowKeys, selectedRows) => {
                        let questions = [];
                        for (let i = 0; i < selectedRows.length; i++) {
                            questions.push(selectedRows[i].snapshot);
                        }
                        this.questions = questions;
                        this.$nextTick(()=>{
                            this.resize();
                        })
                    },
                },
            }
        },
        methods: {
            handleSearch(filter) {
                this.$refs.table.handleSearch(filter)
            },
            handleOperate(type, row) {
                switch (type) {
                    case 'check':
                        this.$router.push({name: '在线考试', params: {id: row.id}})
                        break;
                    case 'delete':
                        deleteCollectQuestion(row.id)
                            .then(res => {
                                this.$message.success('删除成功');
                                this.$refs.table.reFetch()
                                    .then(res=>{
                                        this.$nextTick(()=>{
                                            this.resize();
                                        })
                                    });
                            });
                        break;
                }
            },
            resize() {
                let height0 = $($('.ant-card')[0]).height();
                let height1 = $($('.ant-card')[1]).height();
                let height = height0 > height1 ? height0 : height1;
                $('#split-pane-container').height(height);
            },
        },
        mounted() {
        }
    }
</script>
