<template>
    <a-date-picker
            style="width: 190px"
            showTime
            format="YYYY-MM-DD HH:mm:ss"
            :placeholder="placeholder"
            @change="onChange"
            @ok="onOk"
            :value="getValue(value)"
    />
</template>

<script>
    export default {
        props: {
            value: {//秒时间戳
                type: Number,
                required: false,
            },
            placeholder:{
                required:false,
                type:String,
                default(){
                    return '点击选择'
                }
            }
        },
        model: {
            prop: 'value',
            event: 'change'
        },
        methods: {
            getValue(value) {
                return isNaN(value) ? value : moment(value * 1000);
            },
            onChange(value, dateString) {
                let timestamp = value ? value.valueOf() / 1000 : null;
                console.log(parseInt(timestamp));
                this.$emit('change', parseInt(timestamp));
            },
            onOk(value) {
                let timestamp = value ? value.valueOf() / 1000 : null;
                console.log(parseInt(timestamp));
                this.$emit('change', parseInt(timestamp));
            }
        },
    }
</script>
