import axios from '@/libs/api.request'
import store from '@/store'

const baseUrl = 'departments/';

export const getDepartmentList = ({current_page = 0, filter = {}, with: $with}) => {
    let data = {
        current_page,
        filter,
        with: ['parent'].concat($with ? $with : []) //with是js保留词 所以加了$
    }
    data = Object.assign(
        data,
        {
            token: store.state.common.token,
            access_token: store.state.common.access_token
        }
    );
    return axios.request({
        url: baseUrl,
        method: 'get',
        params: data,
    })
}

